import axios from 'axios'

export type User = {
  _id: string
  email: string
  role: 'user' | 'admin'
}

export type Service = {
  _id: string
  name: string
  duration?: number // Dauer in Minuten
}

export type Booking = {
  _id: string
  user: string
  serviceId: string
  dateTime: string
}

export type StaffBooking  = {
  _id: string
  user: {
    email: string
  }
  service: {
    _id: string
    name: string
    duration?: number
  }
  dateTime: string
}

const api = axios.create({
  baseURL: 'http://localhost:5000/api',
  headers: {
    'Content-Type': 'application/json',
  },
})

// Token automatisch mitschicken
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token')
  if (token) {
    config.headers.Authorization = `Bearer ${token}`
  }
  return config
})

// SERVICE API

export async function fetchServices(): Promise<Service[]> {
  const res = await api.get('/services')
  return res.data.services
}

export async function createService(name: string, duration: number, token: string) {
  const res = await api.post('/services', { name, duration }, {
    headers: {
      Authorization: `Bearer ${token}`
    }
  })
  return res.data
}

export async function deleteServiceById(id: string, token: string) {
  const res = await api.delete(`/services/${id}`, {
    headers: {
      Authorization: `Bearer ${token}`
    }
  })
  return res.data
}

// BOOKING API

export async function createBooking(serviceId: string, dateTime: string, staffId: string, token: string) {
  const res = await api.post('/bookings', {
    serviceId,
    dateTime,
    staffId,
  }, {
    headers: {
      Authorization: `Bearer ${token}`
    }
  })
  return res.data
}

export async function getUserBookings(token: string): Promise<Booking[]> {
  const res = await api.get('/bookings/user', {
    headers: {
      Authorization: `Bearer ${token}`
    }
  })
  return res.data.bookings
}

export async function getAllBookings(token: string): Promise<Booking[]> {
  const res = await api.get('/bookings/all', {
    headers: {
      Authorization: `Bearer ${token}`
    }
  })
  return res.data.bookings
}

export async function deleteBooking(id: string, token: string) {
  const res = await api.delete(`/bookings/${id}`, {
    headers: {
      Authorization: `Bearer ${token}`
    }
  })
  return res.data
}

// AUTH API

export async function login(email: string, password: string) {
  const res = await api.post('/login', { email, password })
  return res.data // { token, user: { email, role } }
}

export async function register(
  email: string,
  password: string,
  name?: string,
  address?: string,
  phone?: string,
  role: 'user' | 'staff' | 'admin' = 'user'
) {
  const res = await api.post('/register', {
    email,
    password,
    name,
    address,
    phone,
    role,
  });
  return res.data;
}

// ➡ Alle Mitarbeiter abrufen
export async function fetchStaff(): Promise<User[]> {
  const res = await api.get('/users/staff') 
  return res.data.users
}

// ➡ Buchungen für Mitarbeiter abrufen
export async function getStaffBookings(token: string): Promise<StaffBooking[]> {
  const res = await api.get('/bookings/staff', {
    headers: {
      Authorization: `Bearer ${token}`
    }
  })
  return res.data.bookings
}

// ➡ Alle Nutzer abrufen
export async function fetchAllUsers(token: string) {
  const res = await api.get('/users', {
    headers: { Authorization: `Bearer ${token}` }
  })
  return res.data.users
}

// ➡ Mitarbeiterrolle aktualisieren
export async function updateUserRole(userId: string, role: string, token: string) {
  const res = await api.patch(`/users/${userId}/role`, { role }, {
    headers: { Authorization: `Bearer ${token}` }
  })
  return res.data.user
}

// ➡ Buchungen bearbeiten
export async function updateBooking(
  id: string,
  data: { serviceId?: string; dateTime?: string },
  token: string
) {
  const res = await api.patch(`/bookings/bookings/${id}`, data, {
    headers: { Authorization: `Bearer ${token}` }
  })
  return res.data.booking
}
